html,
body {
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background-color: #1a0f00;
  background-image: url('../../images/Background.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

/* Main container */
.Grimoire {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  image-rendering: pixelated;
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-align: center;
}

.grimoire-title ,
.title-shifted {
  position: absolute;
  top: -70%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-shadow: 2px 2px #000;
  pointer-events: none;
  text-align: center;
  width: 100%;
  padding: 1rem 0;
}

.grimoire-title.modal-hidden,
.title-shifted.modal-hidden {
  visibility: hidden;
}

/* Altar layout */
.altar {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 2vw;
  margin-bottom: 2vh;
  position: relative;
  z-index: 3;
}

/* Candle image */
.candle-img {
  width: 100%;
  height: 100%;
  image-rendering: pixelated;
  z-index: 2;
}

/* Candle wrapper */
.candle-wrapper {
  position: relative;
  top: -10%;
  width: 40%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

/* Left candle */
.left-candle {
  right: -5%;
}

/* Right candle */
.right-candle {
  left: -5%;
}

/* Glow effect per candle */
.candle-wrapper::after {
  content: "";
  position: absolute;
  top: 3%;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 150, 0.8) 0%, rgba(255, 200, 0, 0.4) 60%, transparent 100%);
  animation: candleGlow 1.5s infinite ease-in-out;
  pointer-events: none;
  z-index: 1;
}

@keyframes candleGlow {

  0%,
  100% {
    opacity: 0.6;
    transform: translateX(-50%) scale(1);
    box-shadow: 0 0 6px rgba(255, 255, 150, 0.6), 0 0 12px rgba(255, 200, 0, 0.4);
  }

  50% {
    opacity: 1;
    transform: translateX(-50%) scale(1.2);
    box-shadow: 0 0 10px rgba(255, 255, 150, 0.8), 0 0 20px rgba(255, 200, 0, 0.6);
  }
}

/* Book image styling */
.book-img,
.book-img-hidden {
  width: 40vw;
  height: auto;
  image-rendering: pixelated;
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1);
  transition: filter 0.3s ease;
}

.book-img-hidden {
  visibility: hidden;
}

.book-img:hover {
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
}

/* Optional content below the altar */
.content {
  margin-top: 20%;
  max-width: 80%;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('../../images/GrimoireOpenedBook.png');
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: darken;
  background-size: 80%;
  z-index: 999;
  display: flex;
  flex-direction: column;
  /* Stack vertically */
  justify-content: flex-start;
  /* Align modal content to top */
  align-items: center;
  animation: riseFromBook 0.6s ease-out;
  overflow-y: auto;
  padding-top: 6rem;
  /* Push modal content below title */
}

@keyframes riseFromBook {
  0% {
    transform: scale(0.5) translateY(100px);
    opacity: 0;
  }

  100% {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.modal-content {
  background: rgba(0, 0, 0, 0.6);
  width: 55vw;
  max-width: 55vw;
  height: 80vh;
  padding: 40px;
  border-radius: 8px;
  text-align: center;
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  display: flex;
  flex-direction: row;
}

.modal-menu {
  background: rgba(0, 0, 0, 0.8);
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  padding: 20px;
  box-shadow: 2px 0 6px #000;
  image-rendering: pixelated;
  transition: width 0.3s ease;
  overflow: hidden;
  width: 20%;
  font-size: clamp(1rem, 2vw, 1.5rem);
}

.modal-menu button,
.modal-menu li button {
  font-size: inherit;
}

.menu-toggle {
  background: none;
  border: none;
  color: #f3e9d2;
  font-size: 1.25rem;
  cursor: pointer;
  margin-bottom: 1rem;
}

.modal-menu ul {
  list-style: none;
  padding: 0;
}

.modal-menu li {
  margin-bottom: 1rem;
}

.modal-menu li button {
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  cursor: pointer;
  text-align: left;
  width: 100%;
}

.modal-main {
  flex-grow: 1;
  padding: 40px;
  background: rgba(0, 0, 0, 0.6);
  color: #f3e9d2;
}

.post-section {
  padding: 1rem;
  flex: 1;
  overflow-y: auto;
}

.post-content article {
  margin-bottom: 2rem;
  border-bottom: 1px solid #ccc;
}

/* Responsive layout for tablets and phones */
@media (max-width: 768px) {
  .modal-content {
    flex-direction: column;
    width: 55vw;
    max-width: 55vw;
    height: auto;
    padding: 1rem;
  }

  .modal-menu {
    width: 100%;
    font-size: 1.2rem;
    padding: 1rem;
  }

  .modal-main {
    padding: 1rem;
  }

  .book-img,
  .book-img-hidden {
    width: 80vw;
  }

  .grimoire-title {
    font-size: 1.5rem;
    top: 1rem;
  }

  .altar {
    display: flex;
    bottom: 10%;
    justify-content: center;
    align-items: self-start;
    gap: 2vw;
    margin-bottom: 2vh;
    position: relative;
    z-index: 3;
  }

  .grimoire-title img,
  .title-shifted img{
    position: absolute;
    top: -20%;
    size: -20%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    font-size: clamp(1.5rem, 3vw, 2.5rem);
    font-family: 'Press Start 2P', monospace;
    color: #f3e9d2;
    text-shadow: 2px 2px #000;
    pointer-events: none;
    text-align: center;
    width: 100%;
    padding: 1rem 0;
  }

  .grimoire-title.modal-hidden,
  .title-shifted.modal-hidden {
    visibility: hidden;
  }

}

/* Responsive layout for large screens */
@media (min-width: 1024px) {
  .modal-content {
    flex-direction: row;
    width: 55vw;
    max-width: 55vw;
    height: 80vh;
    padding: 40px;
  }

  .modal-menu {
    width: 20%;
    font-size: 1.5rem;
  }

  .modal-main {
    width: 80%;
    padding: 40px;
  }

  .book-img,
  .book-img-hidden {
    width: 40vw;
  }

  .grimoire-title img,
  .title-shifted img {
    position: absolute;
    top: -60%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    font-size: clamp(1.5rem, 3vw, 2.5rem);
    font-family: 'Press Start 2P', monospace;
    color: #f3e9d2;
    text-shadow: 2px 2px #000;
    pointer-events: none;
    text-align: center;
    width: 100%;
    padding: 1rem 0;
  }

  .grimoire-title.modal-hidden,
  .title-shifted.modal-hidden {
    visibility: hidden;
  }
}